.leftMenu{
    width: 420px;
    height: 114px; 
    position: relative;
    left: 8px;
    top: 8px;
    border-radius: 8px;
    color: #A7A6A6;
    background: #121212;
    padding: 20px 25px;
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
}
.leftMenu .logoHome{
    display: flex;
    align-items: center;
    position: relative;
    bottom: 5px;
}

.leftMenu .logoHome i{
    font-size: 25px; 
    padding-right: 16px; 
}
.leftMenu .logoHome h2{
    font-size: 16px;
}
.leftMenu .searchBox{
    display: flex;
    align-items: center;
    position: relative;
    top: 8px;
}
.leftMenu .searchBox i{
    font-size: 25px;
    padding-right: 16px; 
    padding-top: 4px;
}
.leftMenu .searchBox h2{
    font-size: 16px;
}